.hero{
    display: flex;
    flex-wrap: wrap-reverse;
    justify-content: space-around;
    align-items: center;
    height: 80vh;
    .left{
        h1{
            color: #42446E;
            font-size: 60px;
            font-style: normal;
            font-weight: 700;
            line-height: 65px; 
            letter-spacing: -1px;
            span {
                  font-weight: 700;
                  background: linear-gradient(to right, rgb(64, 112, 255), #9e0dff 33%, #07f255);
                  background-size: 200% auto;
                  -webkit-background-clip: text;
                  background-clip: text;
                  color: transparent;
                  animation: gradient 3s linear infinite;
                
              
                @keyframes gradient {
                  0% {
                    background-position: 0% 75%;
                  }
                  50% {
                    background-position: 100% 50%;
                  }
                  100% {
                    background-position: 0% 70%;
                  }
                }
              }
              
        }
    }
    .image{
         
        width: 400px;
        height: 400px;
        border: 5px solid blueviolet;
        padding: 10px;
        border-radius: 100%;
       
        img{
            width: 100%;
            display: block;
            border-radius: 100%;
        }
    }
}

@media screen and (max-width: 1360px) {
  .hero{
    .left{
      h1{
        font-size: 45px;
      }
    }
  }
}
@media screen and (max-width: 1160px) {
  .hero{
    .left{
      h1{
        font-size: 40px;
        line-height: 48px;
      }
    }
    .image{
      width: 320px;
      height: 320px;
    }
  }
}
@media screen and (max-width: 962px) {
  .hero{
    .left{
      h1{
        font-size: 35px;
        line-height: 40px;
      }
    }
    .image{
      width: 300px;
      height: 300px;
    }
  }
}
@media screen and (max-width: 768px) {
  .hero{
    justify-content: unset;
    padding-top: 70px;
    align-items: unset;
    gap: 40px;
  }
}
@media screen and (max-width: 355px) {
  .hero{
   .left{
    h1{
      font-size: 25px;
    }
      
   }
   .image{
     width: 250px;
     height: 250px;
   }
  }
}